import { CST } from "../CST";

export class InstructionsScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.INSTRUCTIONS
        });
    }

    escButton!: { [index: string]: Phaser.Input.Keyboard.Key };

    create() {
        this.add.image(0, 0, CST.IMAGE.INSTRUCTIONS).setOrigin(0).setDepth(0);

        let playButton = this.add.image(this.game.renderer.width - 120, this.game.renderer.height - 35, CST.IMAGE.OK).setDepth(2);

        playButton.setInteractive();

        playButton.on("pointerup", () => {
            this.scene.start(CST.SCENES.CHARACTER1);
        });

        //@ts-ignore
        this.escButton = this.input.keyboard.addKeys("ESC");
    }

    update() {
        if (this.escButton.ESC.isDown) {
            this.sound.stopAll();
            this.scene.start(CST.SCENES.MENU);
        }
    }
}