import { Physics } from "phaser";

export class Vegeta {
    player!: Phaser.Physics.Arcade.Sprite;
    formerPosition: boolean;
    anims!: Phaser.Animations.AnimationManager;
    attack: boolean;
    health: number;
    ki: number;
    kiBlastImage!: Phaser.Physics.Arcade.Group;
    flip: number;
    specialImage!: Phaser.Physics.Arcade.Group;
    time!: Phaser.Time.Clock;
    maximumKi: number;
    minimumKi: number;
    kiBlastSpecialImage!: Phaser.Physics.Arcade.Group;

    constructor(formerPosition: boolean,
        health: number,
        ki: number,
        flip: number,
        maximumKi: number,
        minimumKi: number) {
        this.attack = false;
        this.health = health;
        this.ki = ki;
        this.flip = flip;
        this.formerPosition = formerPosition;
        this.maximumKi = maximumKi;
        this.minimumKi = minimumKi;
    }

    createWalk() {
        this.anims.create({
            key: 'vegeta_walk',
            frameRate: 10,
            repeat: -1,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [32],
                zeroPad: 2
            }),
        });
    }

    walkRight() {
        this.player.setVelocityX(160); //Viteza de deplasare
        this.player.anims.play("vegeta_walk", true);
        this.player.flipX = false;
        this.attack = false;
        this.flip = -1;
    }

    walkLeft() {
        this.player.setVelocityX(-160); //Viteza de deplasare
        this.player.anims.play('vegeta_walk', true);
        this.player.flipX = true;
        this.attack = false;
        this.flip = 1;
    }

    createJump() {
        this.anims.create({
            key: 'vegeta_jump',
            frameRate: 5,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [19],
                zeroPad: 2
            })
        });
    }

    jump() {
        if (this.player.body.touching.down === true)
            this.player.setVelocityY(-330);
        this.player.anims.play('vegeta_jump', true);
        this.attack = false;
    }

    createStop() {
        this.anims.create({
            key: 'vegeta_stop',
            frameRate: 3,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [4],
                zeroPad: 2
            })
        });
    }

    stop() {
        this.player.anims.play('vegeta_stop', true);
        this.attack = false;
    }

    createFalling() {
        this.anims.create({
            key: 'vegeta_falling',
            frameRate: 3,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [22],
                zeroPad: 2
            })
        });
    }

    falling() {
        this.player.anims.play('vegeta_falling', true);
        this.attack = false;
    }

    createGuarding() {
        this.anims.create({
            key: 'vegeta_guarding',
            frameRate: 3,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [5],
                zeroPad: 2
            })
        });
    }

    guarding() {
        this.player.setVelocityX(0);
        this.player.anims.play('vegeta_guarding', true);
        this.attack = false;
    }

    createPunching() {
        this.anims.create({
            key: 'vegeta_punching',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [32, 33, 34],
                zeroPad: 2
            })
        });
    }

    punching() {
        this.player.setVelocityX(0);
        this.player.anims.play('vegeta_punching', true);
        this.attack = true;
    }

    createPunchingWhileInAir() {
        this.anims.create({
            key: 'vegeta_punching_while_in_air',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [39, 40, 41],
                zeroPad: 2
            })
        });
    }

    punchingWhileInAir() {
        this.player.setVelocityX(0);
        this.player.anims.play('vegeta_punching_while_in_air', true);
        this.attack = true;
    }

    createHitWithLeg() {
        this.anims.create({
            key: 'vegeta_hit_with_leg',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [42, 43, 44],
                zeroPad: 2
            })
        });
    }

    hitWithLeg() {
        this.player.setVelocityX(0);
        this.player.anims.play('vegeta_hit_with_leg', true);
        this.attack = true;
    }

    createHitWithLegWhileInAir() {
        this.anims.create({
            key: 'vegeta_hit_with_leg_while_in_air',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [52, 53, 54],
                zeroPad: 2
            })
        });
    }


    hitWithLegWhileInAir() {
        this.player.setVelocityX(0);
        this.player.anims.play('vegeta_hit_with_leg_while_in_air', true);
        this.attack = true;
    }

    createChargeKi() {
        this.anims.create({
            key: 'vegeta_charge_ki',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [82, 83],
                zeroPad: 2
            }),
        });
    }

    chargeKi() {
        this.player.setVelocityX(0);
        this.player.setVelocityY(-15);
        this.player.anims.play("vegeta_charge_ki", true);
        this.attack = false;
    }

    createHurt() {
        this.anims.create({
            key: 'vegeta_hurt',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [67],
                zeroPad: 2
            }),
        });
    }

    hurt() {
        this.player.setVelocityX(0)
        this.player.anims.play("vegeta_hurt", true);
    }

    createKiBlast() {
        this.anims.create({
            key: 'vegeta_ki_blast',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [57],
                zeroPad: 2
            }),
        });
    }

    kiBlast() {
        if (this.kiBlastImage.getChildren().length < 1 && (this.ki + 10 > this.minimumKi)) {
            this.player.setVelocityX(0);
            this.player.anims.play("vegeta_ki_blast", true);
            let x = this.player.x + this.player.width * (-this.flip);
            let y = this.player.y;
            let kiB = this.kiBlastImage.create(x, y, "vegeta", "vegeta_62.png");
            let angle = this.computeAngle();
            kiB.setAngle(angle);
            x = (-800) * this.flip;
            kiB.setVelocityX(x);
            kiB.setVelocityY(-100);
            kiB.allowGravity = false;
            this.ki -= 5;
        }
    }

    computeAngle() {
        if (this.flip == -1)
            return -50;
        else
            return 140;
    }

    createSpecialAttack() {
        this.anims.create({
            key: 'vegeta_special',
            frameRate: 10,
            frames: this.anims.generateFrameNames('vegeta', {
                prefix: 'vegeta_',
                suffix: '.png',
                frames: [84, 85, 86, 87, 98, 99, 64],
                zeroPad: 2
            }),
        });
    }

    specialAttack() {
        this.player.body.stop();
        if (this.specialImage.getChildren().length < 1 && (this.ki + 10 >= this.maximumKi) && this.player.body.touching.down) {
            this.player.anims.play("vegeta_special", true);
            //@ts-ignore
            this.player.body.allowGravity = false;
            // this.player.body.blocked.down = true;
            this.player.body.stop();
            let x: number;
            let y: number;
            if (this.flip == -1) {
                x = (this.player.x + this.player.width) + 195;
                y = (this.player.y - 7);
            }
            else {
                x = this.player.x + this.player.width - 305;
                y = this.player.y - 7;
            }
            this.time.addEvent({
                delay: 800,
                callback: () => {
                    let special = this.specialImage.create(x, y, "vegeta", "vegeta_90.png");
                    special.setAngle(this.computeSpecialAngle());
                    this.time.addEvent({
                        delay: 600,
                        callback: () => {
                            special.destroy();
                            //@ts-ignore
                            this.player.body.allowGravity = true;
                            // this.player.body.blocked.down = false;
                        }
                    });
                }
            });
            this.ki -= 100;
        }
    }

    computeSpecialAngle() {
        if (this.flip == -1)
            return 0;
        else
            return 180;
    }

    bodyTouchingDown() {
        return this.player.body.touching.down;
    }

    inAir() {
        return this.player.body.y < 536;
    }

    setVelocityX(x: number) {
        this.player.setVelocityX(x);
    }

    setVelocityY(y: number) {
        this.player.setVelocityY(y);
    }

    setKiBlastImage(kiBlastImage: Phaser.Physics.Arcade.Group) {
        this.kiBlastImage = kiBlastImage;
    }

    setKiBlastSpecialImage(kiBlastSpecialImage: Phaser.Physics.Arcade.Group) {
        this.kiBlastSpecialImage = kiBlastSpecialImage;
    }

    getKiLevel() {
        return this.ki;
    }

    modifyKiLevel(amount: number) {
        if (this.ki)
            this.ki += amount;
    }

    modifyHealth(amount: number) {
        this.health += amount;
    }

    getHealthLevel() {
        return this.health;
    }

    setSpecialImage(specialImage: Phaser.Physics.Arcade.Group) {
        this.specialImage = specialImage;
    }

    setPlayer(player: Phaser.Physics.Arcade.Sprite) {
        this.player = player;
        if (this.formerPosition === true)
            this.player.flipX = true;
    }

    getAtlas() {
        return "vegeta";
    }

    getImage() {
        return "vegeta_02.png";
    }

    setTime(time: Phaser.Time.Clock) {
        this.time = time;
    }

    setAnims(anims: Phaser.Animations.AnimationManager) {
        this.anims = anims;
        this.createWalk();
        this.createJump();
        this.createFalling();
        this.createStop();
        this.createGuarding();
        this.createPunching();
        this.createHitWithLeg();
        this.createHitWithLegWhileInAir();
        this.createPunchingWhileInAir();
        this.createChargeKi();
        this.createHurt();
        this.createKiBlast();
        this.createSpecialAttack();
    }

}