import { CST } from "../CST";
export class MenuScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.MENU
        });
    }

    init() {
    }

    create() {

        this.add.image(0, 0, CST.IMAGE.TITLE).setOrigin(0).setDepth(1);

        this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.20, CST.IMAGE.LOGO).setDepth(2);

        let playButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2, CST.IMAGE.PLAY).setDepth(2);


        this.sound.play(CST.AUDIO.TITLE, {
            volume: 0.1,
            loop: true
        });

        playButton.setInteractive();

        playButton.on("pointerup", () => {
            this.scene.start(CST.SCENES.INSTRUCTIONS);

        });
    }
}