import { Physics } from "phaser";

export class Frieza {
    player!: Phaser.Physics.Arcade.Sprite;
    formerPosition: boolean;
    anims!: Phaser.Animations.AnimationManager;
    attack: boolean;
    health: number;
    ki: number;
    kiBlastImage!: Phaser.Physics.Arcade.Group;
    flip: number;
    specialImage!: Phaser.Physics.Arcade.Group;
    time!: Phaser.Time.Clock;
    maximumKi: number;
    minimumKi: number;
    kiBlastSpecialImage!: Phaser.Physics.Arcade.Group;

    constructor(formerPosition: boolean,
        health: number,
        ki: number,
        flip: number,
        maximumKi: number,
        minimumKi: number) {
        this.attack = false;
        this.health = health;
        this.ki = ki;
        this.flip = flip;
        this.formerPosition = formerPosition;
        this.maximumKi = maximumKi;
        this.minimumKi = minimumKi;
    }

    createWalk() {
        this.anims.create({
            key: 'frieza_walk',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [17],
                zeroPad: 2
            }),
        });
    }

    walkRight() {
        this.player.setVelocityX(160);
        this.player.anims.play('frieza_walk', true);
        this.player.flipX = false;
        this.attack = false;
        this.flip = -1;
    }

    walkLeft() {
        this.player.setVelocityX(-160);
        this.player.anims.play('frieza_walk', true);
        this.player.flipX = true;
        this.attack = false;
        this.flip = 1;
    }

    createJump() {
        this.anims.create({
            key: 'frieza_jump',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [28],
                zeroPad: 2
            }),
        });
    }

    jump() {
        if (this.player.body.touching.down === true)
            this.player.setVelocityY(-330);
        this.player.anims.play('frieza_jump', true);
        this.attack = false;
    }

    createStop() {
        this.anims.create({
            key: 'frieza_stop',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [1],
                zeroPad: 2
            }),
        });
    }

    stop() {
        this.player.anims.play('frieza_stop', true);
        this.attack = false;
    }

    createFalling() {
        this.anims.create({
            key: 'frieza_falling',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [15],
                zeroPad: 2
            }),
        });
    }

    falling() {
        this.player.anims.play('frieza_falling', true);
        this.attack = false;
    }

    createGuarding() {
        this.anims.create({
            key: 'frieza_guarding',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [45],
                zeroPad: 2
            }),
        });
    }

    guarding() {
        this.player.setVelocityX(0);
        this.player.anims.play('frieza_guarding', true);
        this.attack = false;
    }

    createPunching() {
        this.anims.create({
            key: 'frieza_punching',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [20, 21, 22],
                zeroPad: 2
            }),
        });
    }

    punching() {
        this.player.setVelocityX(0);
        this.player.anims.play('frieza_punching', true);
        this.attack = true;
    }

    createPunchingWhileInAir() {
        this.anims.create({
            key: 'frieza_punching_while_in_air',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [32, 33, 34],
                zeroPad: 2
            }),
        });
    }

    punchingWhileInAir() {
        this.player.setVelocityX(0);
        this.player.anims.play('frieza_punching_while_in_air', true);
        this.attack = true;
    }

    createHitWithLeg() {
        this.anims.create({
            key: 'frieza_hit_with_leg',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [10, 11, 12],
                zeroPad: 2
            }),
        });
    }

    hitWithLeg() {
        this.player.setVelocityX(0);
        this.player.anims.play('frieza_hit_with_leg', true);
        this.attack = true;
    }

    createHitWithLegWhileInAir() {
        this.anims.create({
            key: 'frieza_hit_with_leg_while_in_air',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [29, 30, 31],
                zeroPad: 2
            }),
        });
    }

    hitWithLegWhileInAir() {
        this.player.setVelocityX(0);
        this.player.anims.play('frieza_hit_with_leg_while_in_air', true);
        this.attack = true;
    }

    createChargeKi() {
        this.anims.create({
            key: 'frieza_charge_ki',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [37, 38],
                zeroPad: 2
            }),
        });
    }

    chargeKi() {
        this.player.setVelocityX(0);
        this.player.setVelocityY(-15);
        this.player.anims.play("frieza_charge_ki", true);
        this.attack = false;
    }

    createHurt() {
        this.anims.create({
            key: 'frieza_hurt',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [44],
                zeroPad: 2
            }),
        });
    }

    hurt() {
        this.player.setVelocityX(0);
        this.player.anims.play("frieza_hurt", true);
    }

    createKiBlast() {
        this.anims.create({
            key: 'frieza_ki_blast',
            frameRate: 10,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [41],
                zeroPad: 2
            }),
        });
    }

    kiBlast() {
        if (this.kiBlastImage.getChildren().length < 1 && (this.ki + 10 > this.minimumKi)) {
            this.player.setVelocityX(0);
            this.player.anims.play("frieza_ki_blast", true);
            let x = this.player.x + this.player.width * (-this.flip);
            let y = this.player.y;
            let kiB = this.kiBlastImage.create(x, y, "frieza", "frieza_42.png");
            let angle = this.computeAngle();
            kiB.setAngle(angle);
            x = (-800) * this.flip;
            kiB.setVelocityX(x);
            kiB.setVelocityY(-100);
            kiB.allowGravity = false;
            this.ki -= 5;
        }
    }

    computeAngle() {
        if (this.flip == -1)
            return -50;
        else
            return 140;
    }

    createSpecialAttack() {
        this.anims.create({
            key: 'frieza_special',
            frameRate: 5,
            frames: this.anims.generateFrameNames('frieza', {
                prefix: 'frieza_',
                suffix: '.png',
                frames: [77, 78],
                zeroPad: 2
            }),
        });
    }

    specialAttack() {
        if (this.specialImage.getChildren().length < 1 && (this.ki + 10 >= this.maximumKi)) {
            this.player.anims.play("frieza_special", true);
            //@ts-ignore
            this.player.body.allowGravity = false;
            this.player.body.stop();
            let x: number;
            let y: number;
            if (this.flip == -1) {
                x = (this.player.x + this.player.width);
                y = this.player.y - 10;
            }
            else {
                x = (this.player.x + this.player.width) - 60;
                y = this.player.y - 10;
            }
            this.time.addEvent({
                delay: 400,
                callback: () => {
                    let special = this.kiBlastSpecialImage.create(x, y, "frieza", "frieza_72.png");
                    special.setAngle(this.computeSpecialAngle());
                    special.setVelocityX((-800) * this.flip);
                    special.setVelocityY(-100);
                    this.time.addEvent({
                        delay: 1000,
                        callback: () => {
                            special.destroy();
                            //@ts-ignore
                            this.player.body.allowGravity = true;
                        }
                    });
                }
            });
            this.ki -= 100;
        }
    }

    computeSpecialAngle() {
        if (this.flip == -1)
            return 0;
        else
            return 180;
    }

    bodyTouchingDown() {
        return this.player.body.touching.down;
    }

    inAir() {
        return this.player.body.y < 536;
    }

    setVelocityX(x: number) {
        this.player.setVelocityX(x);
    }

    setVelocityY(y: number) {
        this.player.setVelocityY(y);
    }

    setKiBlastImage(kiBlastImage: Phaser.Physics.Arcade.Group) {
        this.kiBlastImage = kiBlastImage;
    }

    setKiBlastSpecialImage(kiBlastSpecialImage: Phaser.Physics.Arcade.Group) {
        this.kiBlastSpecialImage = kiBlastSpecialImage;
    }

    getKiLevel() {
        return this.ki;
    }

    modifyKiLevel(amount: number) {
        this.ki += amount;
    }

    modifyHealth(amount: number) {
        this.health += amount;
    }

    getHealthLevel() {
        return this.health;
    }

    setSpecialImage(specialImage: Phaser.Physics.Arcade.Group) {
        this.specialImage = specialImage;
    }

    setPlayer(player: Phaser.Physics.Arcade.Sprite) {
        this.player = player;
        if (this.formerPosition === true)
            this.player.flipX = true;
    }

    getAtlas() {
        return "frieza";
    }

    getImage() {
        return "frieza_01.png";
    }

    setTime(time: Phaser.Time.Clock) {
        this.time = time;
    }

    setAnims(anims: Phaser.Animations.AnimationManager) {
        this.anims = anims;
        this.createWalk();
        this.createJump();
        this.createFalling();
        this.createStop();
        this.createGuarding();
        this.createPunching();
        this.createHitWithLeg();
        this.createHitWithLegWhileInAir();
        this.createPunchingWhileInAir();
        this.createChargeKi();
        this.createHurt();
        this.createKiBlast();
        this.createSpecialAttack();
    }

}