import { CST } from "../CST";

export class LoadScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.LOAD
        });
    }

    init() {

    }

    loadImages() {
        this.load.setPath("./assets/image");
        for (let prop in CST.IMAGE) {
            //@ts-ignore
            this.load.image(CST.IMAGE[prop], CST.IMAGE[prop]);
        }
    }

    loadAudio() {
        this.load.setPath("./assets/audio");
        for (let prop in CST.AUDIO) {
            //@ts-ignore
            this.load.audio(CST.AUDIO[prop], CST.AUDIO[prop]);
        }
    }

    loadAtlases() {
        this.load.setPath("./assets/atlas");
        this.load.atlas('vegeta', 'vegeta.png', 'vegeta.json');
        this.load.atlas('goku', 'gokussb.png', 'gokussb.json');
        this.load.atlas('frieza', 'frieza.png', 'frieza.json');
        this.load.atlas('trunks', 'trunks.png', 'trunks.json');
        this.load.atlas('gohan', 'gohan.png', 'gohan.json');
        this.load.atlas('zamasu', 'zamasu.png', 'zamasu.json');
    }


    createLoadingText(width: number, height: number) {
        return this.make.text({
            x: width / 2,
            y: height / 2 - 50,
            text: 'Loading...',
            style: {
                font: '20px monospace',
                fill: '#ffffff'
            }
        });
    }

    createPercentText(width: number, height: number) {
        return this.make.text({
            x: width / 2,
            y: height / 2 - 5,
            text: '0%',
            style: {
                font: '18px monospace',
                fill: '#ffffff'
            }
        });
    }

    createAssetText(width: number, height: number) {
        return this.make.text({
            x: width / 2,
            y: height / 2 + 50,
            text: '',
            style: {
                font: '18px monospace',
                fill: '#ffffff'
            }
        });
    }

    preload() {
        this.loadImages();
        this.loadAudio();
        this.loadAtlases();

        let progressBar = this.add.graphics();
        let progressBox = this.add.graphics();
        progressBox.fillStyle(0x222222, 0.8);
        progressBox.fillRect(240, 270, 320, 50);

        let width = this.cameras.main.width;
        let height = this.cameras.main.height;

        let loadingText = this.createLoadingText(width, height);
        loadingText.setOrigin(0.5, 0.5);

        let percentText = this.createPercentText(width, height);
        percentText.setOrigin(0.5, 0.5);

        let assetText = this.createAssetText(width, height);
        assetText.setOrigin(0.5, 0.5);

        this.load.on('progress', (value: number) => {
            console.log(value);
            progressBar.clear();
            progressBar.fillStyle(0xffffff, 1);
            progressBar.fillRect(250, 280, 300 * value, 30);
            percentText.setText(parseInt(" " + value * 100) + '%');
        });

        this.load.on('fileprogress', (file: Phaser.Loader.File) => {
            // assetText.setText('Loading asset: ' + file.src);
        });

        this.load.on('complete', () => {
            progressBar.destroy();
            progressBox.destroy();
            loadingText.destroy();
            percentText.destroy();
            assetText.destroy();
        });
    }

    create() {
        this.scene.start(CST.SCENES.MENU);
    }


}