import { CST } from "../CST";
import { Goku } from "../characters/goku";
import { Vegeta } from "../characters/vegeta";
import { Frieza } from "../characters/frieza";
import { Trunks } from "../characters/trunks";
import { Gohan } from "../characters/gohan";
import { Zamasu } from "../characters/zamasu";

//@ts-ignore
export let player1;

export class CharacterSelectScene1 extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.CHARACTER1
        });
    }

    escButton!: { [index: string]: Phaser.Input.Keyboard.Key };

    oneCharacterSelected = false;
    create() {
        this.add.image(this.game.renderer.width / 2 - 200, this.game.renderer.height / 2 - 200, CST.IMAGE.SELECT1).setOrigin(0);
        let button1 = this.add.image(this.game.renderer.width / 2 - 50, this.game.renderer.height / 2, CST.IMAGE.GOKU);
        let button2 = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2, CST.IMAGE.VEGETA);
        let button3 = this.add.image(this.game.renderer.width / 2 + 50, this.game.renderer.height / 2, CST.IMAGE.FRIEZA);
        let button4 = this.add.image(this.game.renderer.width / 2 - 50, this.game.renderer.height / 2 + 65, CST.IMAGE.TRUNKS);
        let button5 = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2 + 65, CST.IMAGE.GOHAN);
        let button6 = this.add.image(this.game.renderer.width / 2 + 50, this.game.renderer.height / 2 + 65, CST.IMAGE.ZAMASU);

        button1.setInteractive();

        button2.setInteractive();

        button3.setInteractive();

        button4.setInteractive();

        button5.setInteractive();

        button6.setInteractive();

        button1.on('pointerup', () => {
            if (this.oneCharacterSelected === false) {
                player1 = new Goku(false, 100, -10, -1, 100, 5);
                this.oneCharacterSelected = true;
            }
        });
        button2.on('pointerup', () => {
            if (this.oneCharacterSelected === false) {
                player1 = new Vegeta(false, 100, -10, -1, 100, 5);
                this.oneCharacterSelected = true;
            }
        });
        button3.on('pointerup', () => {
            if (this.oneCharacterSelected === false) {
                player1 = new Frieza(false, 100, -10, -1, 100, 5);
                this.oneCharacterSelected = true;
            }
        });
        button4.on('pointerup', () => {
            if (this.oneCharacterSelected === false) {
                player1 = new Trunks(false, 100, -10, -1, 100, 5);
                this.oneCharacterSelected = true;
            }
        });
        button5.on('pointerup', () => {
            if (this.oneCharacterSelected === false) {
                player1 = new Gohan(false, 100, -10, -1, 100, 5);
                this.oneCharacterSelected = true;
            }
        });
        button6.on('pointerup', () => {
            if (this.oneCharacterSelected === false) {
                player1 = new Zamasu(false, 100, -10, -1, 100, 5);
                this.oneCharacterSelected = true;
            }
        });

        //@ts-ignore
        this.escButton = this.input.keyboard.addKeys("ESC");
    }

    update() {
        if (this.oneCharacterSelected === true) {
            this.oneCharacterSelected = false;
            this.scene.start(CST.SCENES.CHARACTER2);
        }

        if (this.escButton.ESC.isDown) {
            this.sound.stopAll();
            this.scene.start(CST.SCENES.MENU);
        }
    }
}