import { CST } from "../CST";

export let level: string;

export class LevelSelectScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.SELECT
        });
    }

    escButton!: { [index: string]: Phaser.Input.Keyboard.Key };

    create() {
        this.add.image(this.game.renderer.width / 2 - 200, this.game.renderer.height / 2 - 200, CST.IMAGE.LEVEL).setOrigin(0);
        let button1 = this.add.image(this.game.renderer.width / 2 - 50, this.game.renderer.height / 2, CST.IMAGE.ONE);
        let button2 = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2, CST.IMAGE.TWO);
        let button3 = this.add.image(this.game.renderer.width / 2 - 50, this.game.renderer.height / 2 + 65, CST.IMAGE.THREE);
        let button4 = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2 + 65, CST.IMAGE.FOUR);
        let button5 = this.add.image(this.game.renderer.width / 2 - 50, this.game.renderer.height / 2 + 130, CST.IMAGE.FIVE);
        let button6 = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2 + 130, CST.IMAGE.SIX);

        button1.setInteractive();

        button2.setInteractive();

        button3.setInteractive();

        button4.setInteractive();

        button5.setInteractive();

        button6.setInteractive();

        button1.on('pointerup', () => {
            level = "level1.png";
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.LEVEL1, {
                volume: 0.1,
                loop: true
            });
            this.scene.start(CST.SCENES.GAME);
        });
        button2.on('pointerup', () => {
            level = "level2.png";
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.LEVEL2, {
                volume: 0.1,
                loop: true
            });
            this.scene.start(CST.SCENES.GAME);
        });
        button3.on('pointerup', () => {
            level = "level3.png";
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.LEVEL3, {
                volume: 0.1,
                loop: true
            });
            this.scene.start(CST.SCENES.GAME);
        });
        button4.on('pointerup', () => {
            level = "level4.png";
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.LEVEL4, {
                volume: 0.1,
                loop: true
            });
            this.scene.start(CST.SCENES.GAME);
        });
        button5.on('pointerup', () => {
            level = "level5.png";
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.LEVEL5, {
                volume: 0.1,
                loop: true
            });
            this.scene.start(CST.SCENES.GAME);
        });
        button6.on('pointerup', () => {
            level = "level6.png";
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.LEVEL6, {
                volume: 0.1,
                loop: true
            });
            this.scene.start(CST.SCENES.GAME);
        });

        //@ts-ignore
        this.escButton = this.input.keyboard.addKeys("ESC");
    }

    update() {
        if (this.escButton.ESC.isDown) {
            this.sound.stopAll();
            this.scene.start(CST.SCENES.MENU);
        }
    }
}