import { CST } from "../CST";
import { Data } from "phaser";

export class GameOverScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.OVER
        });
    }

    info!: string;

    init(data: any) {
        this.info = data.message;
    }

    createTryAgainButton() {
        let tryAgainButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height - 100, CST.IMAGE.TRY).setDepth(1);

        tryAgainButton.setInteractive();

        tryAgainButton.on("pointerup", () => {
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.TITLE, {
                volume: 0.1,
                loop: true
            })
            this.scene.start(CST.SCENES.CHARACTER1);
        });
    }

    createGoToMenuButton() {
        let goToMenuButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height - 55, CST.IMAGE.MENU).setDepth(2);

        goToMenuButton.setInteractive();

        goToMenuButton.on("pointerup", () => {
            this.sound.stopAll();
            this.sound.play(CST.AUDIO.TITLE, {
                volume: 0.1,
                loop: true
            })
            this.scene.start(CST.SCENES.MENU);
        });
    }

    displayWhoWon() {
        if (this.info === 'Player 1 won') {
            this.add.image(this.game.renderer.width / 2, this.game.renderer.height - 140, CST.IMAGE.PLAYER1);
        }
        else {
            this.add.image(this.game.renderer.width / 2, this.game.renderer.height - 140, CST.IMAGE.PLAYER2);
        }
    }

    create() {
        this.sound.stopAll();

        this.sound.play(CST.AUDIO.OVER, {
            volume: 0.1,
        });

        this.add.image(0, 0, CST.IMAGE.OVER).setOrigin(0).setDepth(0);

        this.displayWhoWon();

        this.createTryAgainButton();

        this.createGoToMenuButton();
    }
}