import { CST } from "../CST";
import { Vegeta } from "../characters/vegeta";
import { Goku } from "../characters/goku";
import { game } from "../main";
import { player1 } from "../scenes/CharacterSelectScene1"
import { player2 } from "../scenes/CharacterSelectScene2"
import { level } from "./LevelSelectScene";
export class GameScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.GAME
        });
    }
    //@ts-ignore
    platforms;

    keyboard1!: { [index: string]: Phaser.Input.Keyboard.Key };
    keyboard2!: { [index: string]: Phaser.Input.Keyboard.Key };

    //@ts-ignore
    healthMask1; healthMask2; energyMask1; energyMask2; kiBlast1; kiBlast2; special1; special2; kiBlastSpecial1; kiBlastSpecial2;

    preload() {
    }

    create() {
        this.add.image(400, 300, level);

        this.platforms = this.physics.add.staticGroup();
        this.platforms.create(400, 600, CST.IMAGE.PLATFORM);

        let healthContainer1 = this.add.sprite(100, 25, CST.IMAGE.HEALTHCONTAINER);
        let healthBar1 = this.add.sprite(healthContainer1.x, healthContainer1.y, CST.IMAGE.HEALTHBAR);
        this.healthMask1 = this.add.sprite(healthBar1.x, healthBar1.y, CST.IMAGE.HEALTHBAR);
        this.healthMask1.visible = false;
        healthBar1.mask = new Phaser.Display.Masks.BitmapMask(this, this.healthMask1);

        let healthContainer2 = this.add.sprite(700, 25, CST.IMAGE.HEALTHCONTAINER);
        let healthBar2 = this.add.sprite(healthContainer2.x, healthContainer2.y, CST.IMAGE.HEALTHBAR);
        this.healthMask2 = this.add.sprite(healthBar2.x, healthBar2.y, CST.IMAGE.HEALTHBAR);
        this.healthMask2.visible = false;
        healthBar2.mask = new Phaser.Display.Masks.BitmapMask(this, this.healthMask2);

        let energyContainer1 = this.add.sprite(90, 45, CST.IMAGE.ENERGYCONTAINER);
        let energyBar1 = this.add.sprite(energyContainer1.x, energyContainer1.y, CST.IMAGE.ENERGYBAR);
        this.energyMask1 = this.add.sprite(energyBar1.x, energyBar1.y, CST.IMAGE.ENERGYBAR);
        this.energyMask1.visible = false;
        energyBar1.mask = new Phaser.Display.Masks.BitmapMask(this, this.energyMask1);

        let energyContainer2 = this.add.sprite(690, 45, CST.IMAGE.ENERGYCONTAINER);
        let energyBar2 = this.add.sprite(energyContainer2.x, energyContainer2.y, CST.IMAGE.ENERGYBAR);
        this.energyMask2 = this.add.sprite(energyBar2.x, energyBar2.y, CST.IMAGE.ENERGYBAR);
        this.energyMask2.visible = false;
        energyBar2.mask = new Phaser.Display.Masks.BitmapMask(this, this.energyMask2);

        let playerSprite1 = this.physics.add.sprite(100, 450, player1.getAtlas(), player1.getImage());
        playerSprite1.setBounce(0.2);
        playerSprite1.setCollideWorldBounds(true);

        let playerSprite2 = this.physics.add.sprite(700, 450, player2.getAtlas(), player2.getImage());
        playerSprite2.setBounce(0.2);
        playerSprite2.setCollideWorldBounds(true);

        this.physics.add.collider(playerSprite1, this.platforms);
        this.physics.add.collider(playerSprite2, this.platforms);

        player1.setPlayer(playerSprite1);
        player1.setTime(this.time);
        player1.setAnims(this.anims);

        player2.setPlayer(playerSprite2);
        player2.setTime(this.time);
        player2.setAnims(this.anims);

        //@ts-ignore
        this.keyboard1 = this.input.keyboard.addKeys("W, A, S, D, T, Y, U, G, H, ESC");
        //@ts-ignore
        this.keyboard2 = this.input.keyboard.addKeys("UP, DOWN, LEFT, RIGHT, I, O, P, J, K");

        // this.physics.world.addCollider(player1.player, player2.player);

        this.healthMask1.x = player1.health;
        this.healthMask2.x = player2.health;

        this.energyMask1.x = player1.ki;
        this.energyMask2.x = player2.ki;

        this.kiBlast1 = this.physics.add.group();
        player1.setKiBlastImage(this.kiBlast1);
        this.physics.add.collider(player2.player, this.kiBlast1, (player, kiBlast) => {
            kiBlast.destroy();
        });

        this.kiBlast2 = this.physics.add.group();
        player2.setKiBlastImage(this.kiBlast2);
        this.physics.add.collider(player1.player, this.kiBlast2, (player, kiBlast) => {
            kiBlast.destroy();
        });

        this.special1 = this.physics.add.staticGroup();
        player1.setSpecialImage(this.special1);
        this.physics.add.collider(player2.player, this.special1, () => {
            player2.modifyHealth(-1.5);
        });

        this.special2 = this.physics.add.staticGroup();
        player2.setSpecialImage(this.special2);
        this.physics.add.collider(player1.player, this.special2, () => {
            player1.modifyHealth(-1.5);
        });

        this.physics.add.collider(this.kiBlast1, this.kiBlast2, (kiB1, kiB2) => {
            kiB1.destroy();
            kiB2.destroy();
        });

        this.kiBlastSpecial1 = this.physics.add.group();
        player1.setKiBlastSpecialImage(this.kiBlastSpecial1);
        this.physics.add.collider(player2.player, this.kiBlastSpecial1, (player, kiBlastSpecial) => {
            kiBlastSpecial.destroy();
        });

        this.kiBlastSpecial2 = this.physics.add.group();
        player2.setKiBlastSpecialImage(this.kiBlastSpecial2);
        this.physics.add.collider(player1.player, this.kiBlastSpecial2, (player, kiBlastSpecial) => {
            kiBlastSpecial.destroy();
        });

    }

    update() {
        this.energyMask1.x = player1.getKiLevel();
        this.healthMask1.x = player1.getHealthLevel();

        this.energyMask2.x = player2.getKiLevel();
        this.healthMask2.x = player2.getHealthLevel();

        this.checkForZeroHealth();

        this.movePlayer1();
        this.movePlayer2();

        this.checkAgainstEnemy();

        this.returnToMenu();
    }
    movePlayer1() {
        if (this.keyboard1.D.isDown === true) {
            player1.walkRight();
        }
        else if (this.keyboard1.A.isDown === true) {
            player1.walkLeft();
        }
        else if (this.keyboard1.S.isDown === true) {
            player1.guarding();
        }
        else if (this.keyboard1.T.isDown === true) {
            if (player1.inAir() === true)
                player1.punchingWhileInAir();
            else
                player1.punching();

        }
        else if (this.keyboard1.Y.isDown === true) {
            if (player1.inAir() === true)
                player1.hitWithLegWhileInAir();
            else
                player1.hitWithLeg();
        }
        else if (this.keyboard1.U.isDown === true) {
            player1.chargeKi();
            if (this.energyMask1.x < 90)
                player1.modifyKiLevel(0.1);
        }
        else if (this.keyboard1.G.isDown === true) {
            player1.kiBlast();
        }
        else if (this.keyboard1.H.isDown === true) {
            player1.specialAttack();
        }
        else {
            player1.setVelocityX(0);
            if (player1.inAir() === true)
                player1.falling();
            else
                player1.stop();
        }

        if (this.keyboard1.W.isDown === true) {
            player1.jump();
        }
    }

    movePlayer2() {
        if (this.keyboard2.LEFT.isDown === true) {
            player2.walkLeft();
        }
        else if (this.keyboard2.RIGHT.isDown === true) {
            player2.walkRight();
        }
        else if (this.keyboard2.DOWN.isDown === true) {
            player2.guarding();
        }
        else if (this.keyboard2.I.isDown === true) {
            if (player2.inAir() === true)
                player2.punchingWhileInAir();
            else
                player2.punching();
        }
        else if (this.keyboard2.O.isDown === true) {
            if (player2.inAir() === true)
                player2.hitWithLegWhileInAir();
            else
                player2.hitWithLeg();
        }
        else if (this.keyboard2.P.isDown === true) {
            player2.chargeKi();
            if (this.energyMask2.x < 690)
                player2.modifyKiLevel(0.1);
        }
        else if (this.keyboard2.J.isDown === true) {
            player2.kiBlast();
        }
        else if (this.keyboard2.K.isDown === true) {
            player2.specialAttack();
        }
        else {
            player2.setVelocityX(0);
            if (player2.inAir() === true)
                player2.falling();
            else
                player2.stop();

        }

        if (this.keyboard2.UP.isDown === true) {
            player2.jump();
        }
    }

    checkAgainstEnemy() {

        if (Math.abs(player1.player.body.x - player2.player.body.x) < 41 && Math.abs(player1.player.y - player2.player.y) < 10) {
            if (player1.attack === true) {
                if (this.player2Guarding() === false) {
                    // player2.hurt();
                    player2.modifyHealth(-0.5);
                }
                else {
                    player2.modifyHealth(-0.5 / 3);
                }
                this.modifyKi(0.1, 0.3);
            }
            if (player2.attack === true) {
                if (this.player1Guarding() === false) {
                    // player1.hurt();
                    player1.modifyHealth(-0.5);
                }
                else {
                    player1.modifyHealth(-0.5 / 3);
                }
                this.modifyKi(0.3, 0.1);
            }
        }
        if (this.keyboard1.U.isDown && Math.abs(player1.player.body.x - player2.player.body.x) < 50 && Math.abs(player1.player.y - player2.player.y) < 25) {
            if (player2.attack === true) {
                player1.modifyHealth(-0.5);
                this.modifyKi(0.3, 0.1);
            }
        }
        if (this.keyboard2.P.isDown && Math.abs(player1.player.body.x - player2.player.body.x) < 50 && Math.abs(player1.player.y - player2.player.y) < 25) {
            if (player1.attack === true) {
                player2.modifyHealth(-0.5);
                this.modifyKi(0.1, 0.3);
            }
        }
        for (let i = 0; i < this.kiBlast1.getChildren().length; i++) {
            let kiB = this.kiBlast1.getChildren()[i];
            if (kiB.x >= 800 || kiB.x <= 0)
                kiB.destroy();
            if (Math.abs(player2.player.x - kiB.x) < 100 && Math.abs(player2.player.y - kiB.y) < 20) {
                // player2.hurt();
                player2.modifyHealth(-1);
            }
        }
        for (let i = 0; i < this.kiBlast2.getChildren().length; i++) {
            let kiB = this.kiBlast2.getChildren()[i];
            if (kiB.x >= 800 || kiB.x <= 0)
                kiB.destroy();
            if (Math.abs(player1.player.x - kiB.x) < 100 && Math.abs(player1.player.y - kiB.y) < 20) {
                // player1.hurt();
                player1.modifyHealth(-1);
            }
        }
        for (let i = 0; i < this.kiBlastSpecial1.getChildren().length; i++) {
            let kiB = this.kiBlastSpecial1.getChildren()[i];
            if (kiB.x >= 800 || kiB.x <= 0)
                kiB.destroy();
            if (Math.abs(player2.player.x - kiB.x) < 100 && Math.abs(player2.player.y - kiB.y) < 20) {
                // player2.hurt();
                player2.modifyHealth(-40);
            }
        }
        for (let i = 0; i < this.kiBlastSpecial2.getChildren().length; i++) {
            let kiB = this.kiBlastSpecial2.getChildren()[i];
            if (kiB.x >= 800 || kiB.x <= 0)
                kiB.destroy();
            if (Math.abs(player1.player.x - kiB.x) < 100 && Math.abs(player1.player.y - kiB.y) < 20) {
                // player1.hurt();
                player1.modifyHealth(-40);
            }
        }
    }

    modifyKi(value1: number, value2: number) {
        if (this.energyMask1.x < 90)
            player1.modifyKiLevel(value1);
        if (this.energyMask2.x < 690)
            player2.modifyKiLevel(value2);
    }

    player1Guarding() {
        return this.keyboard1.S.isDown;
    }

    player2Guarding() {
        return this.keyboard2.DOWN.isDown;
    }

    checkForZeroHealth() {
        if (this.healthMask1.x <= -20) {
            this.scene.start(CST.SCENES.OVER, { message: "Player 2 won" });
        }
        if (this.healthMask2.x <= 580) {
            this.scene.start(CST.SCENES.OVER, { message: "Player 1 won" });
        }
    }

    returnToMenu() {
        if (this.keyboard1.ESC.isDown === true) {
            this.sound.stopAll();
            this.scene.start(CST.SCENES.MENU);
        }
    }
}